.MuiGrid-container {
    min-height: 100vh;
}

.imagen {
background-image: url(./images/Fondo.svg);
width: 100%;
height: 100%;
object-fit: cover;
display: flex;
justify-content: center;
align-items: center;
}
/* contenedor padre de Formulario   */
.formulario{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* icono de usuario  */
.MuiSvgIcon-root {
color: #EE5A24;
width: 50px;
height: 50px;
margin-bottom: 20px;
}

/* Titulo de bienvenida  */
.MuiTypography-h3 {
    color: #EE5A24;
    text-shadow: 0px 3px 6px #00000029;
    margin-bottom: 20px;
}
/*  Input sin estatico */
.MuiInput-underline:before {
    border-bottom: 1px solid grey;
    box-shadow: 0px 3px 6px #00000029;
    opacity: 1;
    width: 100%;
    
  }
  .MuiFormLabel-root {
    color: grey;
  }
  /*  Input con efecto hover */
  .MuiInput-underline:hover:not(.Muidisabled):before {
    border-bottom: 2px solid#f1c40f;
  }
  /*  Input listo para escribir */
  .MuiInput-underline:after {
    border-bottom: 2px solid #f39c12;
  }
  .MuiFormLabel-root.Mui-focused {
    color: #ee5a24;
  }
  .MuiFormControl-root {
    width: 300px;
    margin-bottom: 20px;
  }
  /* Recuoperar contrase√±a  */
  .recoverpsw{
      margin-top: 30px;
      margin-bottom: 40px ;
  }
  .MuiTypography-colorPrimary {
    
    color: #C7C2C2;
}
.MuiTypography-colorPrimary:hover {
    text-decoration: none;
    color: #ee5a24
}
/* CSS boton */
.MuiButton-contained {
    background-color: #e1b12c;
    padding-top: 15px;
    padding-bottom: 15px;
    border-radius: 25px;
    width: 300px;
    box-shadow: 1px 3px 6px #00000029;
  opacity: 1;
  }
  .MuiButton-contained:hover {
    background-color: #fbc531;
  }
  .MuiButton-label{
    
    font: Bold 18px Roboto;
  letter-spacing: 0.36px;
  color: #FEFEFE;
  text-shadow: 0px 3px 6px #00000029;
  text-transform: uppercase;
  opacity: 1;
    }